.info-slider-section-home .txt-content,
.new-business-page .txt-blocks-content .txt-block {
    width: 100%;
}
.half-half-block {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: start;
    -ms-flex-align: start;
    align-items: flex-start;
    -webkit-box-pack: start;
    -ms-flex-pack: start;
    justify-content: flex-start;
}
.half-half-block .content-block,
.half-half-block .media-block {
    padding-bottom: 0 !important;
    padding-top: 0 !important;
}
.pre-heading {
    display: none;
    position: absolute;
}
.circle-green,
.circle-pink {
    border-radius: 50%;
    top: 0;
    z-index: 3;
}
.txt-heading-spacer {
    margin-bottom: 15px;
    margin-top: 20px;
}
@media (min-width: 501px) {
    .txt-heading-spacer {
        margin-bottom: 28px;
    }
}
@media (min-width: 1025px) {
    .new-business-page .background-circles-wrap {
        left: -15vw;
        top: -8.125vw;
    }
    .pre-heading {
        display: block;
        margin-bottom: 30px;
        top: 6.25vw;
    }
    .info-slider-section-home.info-slider-new-business .section-heart-red-svg:not(.smaller-heart-top) {
        bottom: 0;
        left: -18.073vw;
        width: 49.844vw;
    }
    .info-slider-section-home .swiper-nav-btn-wrap-prev {
        left: 78.333vw;
        transform: rotate(90deg);
    }
    .info-slider-section-home .swiper-nav-btn-wrap-next {
        left: 84.375vw;
        transform: rotate(-90deg);
    }
    .info-slider-section-home .txt-size-12 {
        margin-bottom: 3.073vw;
        margin-left: 0.26vw;
    }
    .info-slider-section-home .txt-content {
        margin-left: 0.26vw;
        width: 35.938vw;
    }
    .info-slider-section-home .content-wrap {
        padding-bottom: 10.729vw;
        padding-top: 11.823vw;
    }
    .info-slider-section-home .quotes-img-wrap {
        margin-bottom: 1.354vw;
        margin-left: 0.26vw;
    }
    .info-slider-section-home .txt-size-18 {
        margin-top: 2.292vw;
        padding-left: 1.979vw;
    }
    .info-slider-section-home .txt-size-18:before {
        left: 2px;
        top: calc(0.75em + 2px);
    }
    .info-slider-section-home .section-heart-red-svg:not(.smaller-heart-top) {
        bottom: 0;
        left: -1.927vw;
        width: 23.49vw;
    }
    .info-slider-section-home .section-heart-red-svg.smaller-heart-top {
        bottom: 30.938vw;
        left: 44.375vw;
        width: 4.323vw;
    }
    .info-slider-section-home .section-heart-paths-canvas {
        height: 23.594vw;
        width: 23.177vw;
    }
    .info-slider-section-home .paths-icon-1 {
        bottom: 0;
        right: -9.479vw;
    }
    .info-slider-section-home .paths-icon-2 {
        bottom: 0;
        right: 13.646vw;
    }
    .info-slider-section-home .paths-icon-3 {
        bottom: 23.542vw;
        right: -9.479vw;
    }
    .info-slider-section-home .swiper-nav-btn-wrap {
        top: 36.927vw;
    }
    .txt-heading-spacer {
        margin-bottom: 1.458vw;
        margin-top: 50px;
    }
}
