@media (max-width: 1024px) {
    .circle-border-light-purple,
    .circle-border-light-purple-2,
    .circle-border-pink,
    .circle-light-blue,
    .circle-light-blue-border,
    .circle-light-blue-border-2,
    .circle-light-pink,
    .circle-lighter-blue,
    .circle-lighter-blue-border,
    .circle-lighter-blue-border-2,
    .circle-pink,
    .info-slider-section-home .section-heart-paths-canvas,
    .info-slider-section-home .section-heart-red-svg {
        display: none;
    }
    .info-slider-section-home .bg-image {
        background-position: 40% 0;
    }
    .info-slider-section-home .txt-size-44 {
        letter-spacing: 0.1px;
    }
    .info-slider-section-home .txt-size-18 {
        line-height: 1.85;
    }
}
.info-slider-section-home .swiper-nav-btn,
.info-slider-section-home .swiper-nav-btn-wrap {
    transition: opacity 0.5s cubic-bezier(0.61, 1, 0.88, 1);
}
.info-slider-section-home .bg-image {
    background-position: 50% 0;
    background-size: cover;
    z-index: 1;
}
.info-slider-section-home .swiper-nav-btn.swiper-prev:focus .scroll-btn-img,
.info-slider-section-home .swiper-nav-btn.swiper-prev:hover .scroll-btn-img {
    animation: 1.5s ease-in-out infinite swiper-nav-hover-prev;
